<script>
  import CONFIG from "../../config";

  export default {
    name: "make-patrol-record",
    data() {
      return {
        maintRecord: [],
        maintStaffMap: {},
        patroled: false,  //  是否已经维修过
        loading: false,
        imageUrl: [],
        action: CONFIG.baseUrl + "/maint/uploadFile",
        header: {
          token: uni.getStorageSync("token"),
        },
        formData: {
          fileKey: "patrol-record",
        },
        fileList: [],
        valueResult: "",
        resultList: [
          {
            title: "未完成",
            name: "0"
          },
          {
            title: "维修完成",
            name: "1"
          }
        ],
        resultMap: {
          "0": "未完成",
          "1": "维修完成"
        },
        assetAbout: {
          assetId: "",
          name: "",
          code: "",
          modelName: "",
          installPos: ""
        },
        form: {
          taskId: "",
          sendId: "",
          byqName: "",

          byqUba: "",
          byqUac: "",
          byqUcb: "",

          byqIa: "",
          byqIb: "",
          byqIc: "",

          byqTempA: "",
          byqTempB: "",
          byqTempC: "",

          byqPosunFlag: "",
          byqFangdianFlag: "",
          byqYixiangFlag: "",

          pdfShengyu1: "",
          pdfYongdian1: "",
          pdfShengyu2: "",
          pdfYongdian2: "",
          pdfGonglvyinshu: "",


          pdfDianfangzhaoming: "",
          pdfYingjizhaoming: "",
          pdfPaiqifengshan: "",
          pdfXiaofangqicai: "",
          pdfWeisheng: "",
          pdfFangshusheshi: "",
          pdfTemp: "",

          patrolStaffSign: "",
          patrolTime: "",
          statusFlag: "",
          remarks: "",
          patrolPic: ""

        },
        posunList: [
          {
            title: "正常",
            name: "0"
          },
          {
            title: "有破损",
            name: "1"
          }
        ],
        posunMap: {
          '0': '正常',
          '1': '有破损'
        },
        fangdianList: [
          {
            title: "正常",
            name: "0"
          },
          {
            title: "有放电痕迹",
            name: "1"
          }
        ],
        fangdianMap: {
          '0': '正常',
          '1': '有放电痕迹'
        },
        yixiangList: [
          {
            title: "正常",
            name: "0"
          },
          {
            title: "有异响",
            name: "1"
          }
        ],
        yixiangMap: {
          '0': '正常',
          '1': '有异响'
        },
        normalOrNotList: [
          {
            title: "正常",
            name: "0"
          },
          {
            title: "异常",
            name: "1"
          }
        ],
        normalOrNotMap: {
          '0': '正常',
          '1': '异常'
        },
        background: {
          'background': '#f2f2f8'
        },
        checkedNormalProps: [
          'byqPosunFlag',
          'byqFangdianFlag',
          'byqYixiangFlag',
          'pdfDianfangzhaoming',
          'pdfYingjizhaoming',
          'pdfPaiqifengshan',
          'pdfXiaofangqicai',
          'pdfWeisheng',
          'pdfFangshusheshi'],
        checkedVoltageProps: ['byqUba', 'byqUac', 'byqUcb'],
        checkedTemProps: ['byqTempA', 'byqTempB', 'byqTempC'],
      }
    },
    computed: {
      //  正常与否的集合
      normalCollec() {
        const {
          byqPosunFlag,
          byqFangdianFlag,
          byqYixiangFlag,
          pdfDianfangzhaoming,
          pdfYingjizhaoming,
          pdfPaiqifengshan,
          pdfXiaofangqicai,
          pdfWeisheng,
          pdfFangshusheshi
        } = this.form;

        return {
          byqPosunFlag,
          byqFangdianFlag,
          byqYixiangFlag,
          pdfDianfangzhaoming,
          pdfYingjizhaoming,
          pdfPaiqifengshan,
          pdfXiaofangqicai,
          pdfWeisheng,
          pdfFangshusheshi
        }
      }
    },
    watch: {
      normalCollec: function (nV) {
        const allIsNormal = Object.values(nV).every((item) => {
          return item === '0'
        })

        const hasAbnormal = Object.values(nV).some((item) => {
          return item === '1'
        })

        this.form.statusFlag = allIsNormal ? '0' : ''

        if(hasAbnormal) {
          this.form.statusFlag = '1'
        }
      }
    },
    onLoad(options) {
      this.form.taskId = options.taskId;
      this.form.sendId = options.sendId;
      const user = uni.getStorageSync("user");
      if (user) {
        this.form.patrolStaffSign = user.staffName;
      }
      const now = new Date().getTime();
      this.form.patrolTime = this.$u.timeFormat(now, 'yyyy-mm-dd hh:MM:ss')
    },
    onShow() {
      this.getMaintList();//  获取维修记录
    },
    onReady() {
    },
    methods: {
      statusChange(v) {
        this.checkedNormalProps.forEach(prop => {
          if(v === '0') {
            this.form[prop] = v;
          } else if(v === '1') {
            if(this.form[prop] === '') {
              this.form[prop] = v;
            }
          }
        })
      },
      resultChange() {
      },
      back() {
        uni.navigateBack({
          delta: 1
        })
      },
      onSuccess(data, index, lists, name) {
        if (data.code === 200 && data.data) {
          this.imageUrl.push(data.data);
        }
      },
      onRemove(index, lists, name) {
        this.imageUrl.splice(index, 1);
      },
      //  获取维修的详情
      getMaintList() {
        this.$u
            .post('/maint/getPatrolList', {
              taskId: this.form.taskId
            }).then(res => {
          if (res.code === 200 && res.data) {
            this.patroled = true;
          } else {
            this.patroled = false;
          }
        })
      },
      submitForm() {
        if (this.imageUrl.length) {
          this.form.patrolPic = this.imageUrl.join(',');
        }
        if(this.form.statusFlag === '') {
          this.$refs.uToast.show({
            title: '请选择检查结果',
            type: 'warning'
          });
          return
        }
        for(let i = 0; i < this.checkedVoltageProps.length; i++) {
          let v = this.form[this.checkedVoltageProps[i]];
          if(v && (v < 360 || v > 400)) {
            this.$refs.uToast.show({
              title: '电压应介于 360V 与 400V 之间',
              type: 'warning'
            });
            return;
          }
        }

        for(let i = 0; i < this.checkedTemProps.length; i++) {
          let v = this.form[this.checkedTemProps[i]];
          if(v && v > 70) {
            this.$refs.uToast.show({
              title: '温度不得高于 70 ℃',
              type: 'warning'
            });
            return;
          }
        }

        const now = new Date().getTime();
        this.form.patrolTime = this.$u.timeFormat(now, 'yyyy-mm-dd hh:MM:ss')

        const finalForm = Object.assign({}, this.form);
        this.loading = true;
        this.$u
            .post('/maint/savePatrolRecord', finalForm)
            .then(res => {
              console.log(res);
              if (res.code === 200) {
                this.$refs.uToast.show({
                  title: res.message,
                  type: 'success'
                });
                setTimeout(() => {
                  uni.reLaunch({
                    url: '/pages/home/index'
                  })
                }, 300)
              }
            }).catch(e => {
        }).finally(_ => {
          this.loading = false;
        })
      },
      //  图片预览
      doPreviewImage(images, index) {
        uni.previewImage({
          urls: images,
          current: index,
          success: () => {
          },
          fail: () => {
            uni.showToast({
              title: '预览图片失败',
              icon: 'none'
            });
          }
        });
      }
    }
  }
</script>

